@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

body{
  background: #1A3148;
  color: #CDCED0;
  padding: 0;
  margin: 0;
  font-family: 'Roboto', monospace;
}

.App {
  margin: 0;
  padding: 0;
  text-align: center;
  background: #768FA7;
}

#burger{
  position: relative;
  top: 0;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: #CDCED0;
  color: #1A3148;
  justify-content: center;
  align-items: center;
  column-gap: 5vw;
}
#logo{
  width: 50%;
}
.knappen{

  width: 30%;
}

.linker {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.linker li{
  font-style: normal;
  background-color: #FFF;
  margin: 10px;
  padding: 0.3vh;
  box-sizing: border-box;
}
a:visited{
  color: #1A3148;
}